							scenery_img.push(files[i]._data.lists[0].file.path)
						}else{
							// console.log("i",i)
							// console.log("files.length",files.length)
							if(i < files.length){
								for(let n = i;n < files.length;n++){
									// console.log(n)
									if(files[i]._data.lists[0]){
										scenery_img.push(files[i]._data.lists[0].file.path)
									}else{
										break
									}
								}
							}else{
