							if(res.confirm){
								uni.switchTab({
									url:"../my/my"
								})
							}
						}
					})
				}else{
					uni.showModal({
						title: '提示',
						content: '确定上传商家信息',
						confirmColor: '#0ABB9A',
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
								uni.navigateTo({
									url:'./business'
								})
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				
					}
				}	
				
			}
